.sceneContainer {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 500px;
    width: 100%;
    flex-direction: column;
    overflow: hidden;
}
.sceneContainer2 {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 500px;
    width: 100%;
    max-width: 100%;
    flex-direction: column;
    overflow: hidden;
}
.sceneContainer2 li {
    text-decoration: none;
    list-style-type: none;
}
* {
    box-sizing: border-box;
}
.wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    /* top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) translateY(20px) scale(1.5); */
    overflow: hidden;
}
@media screen and (max-width: 600px) {
    .wrapper {
        transform: scale(1);
    }
}
.scene {
    position: absolute;
    width: 300px;
    height: 300px;
    font-family: 'Raleway', sans-serif;
    font-size: 1rem;
}
[data-typing] {
    position: absolute;
    top: 0;
    /*   border: 1px solid #f55; */
    transform-origin: 0% 0%;
    font-size: 25px;
    margin: 0;
    padding: 0;
    top: 100px;
    line-height: 1;
    /* color: var(--bg); */
}
[data-typing='i'] {
    top: 0;
    left: 50px;
    font-size: 90px;
    height: 70px;
    line-height: 70px;
}
[data-typing='frontend'] {
    top: 3px;
    left: 80px;
    font-size: 27px;
    letter-spacing: 1px;
}
[data-typing='engineer'] {
    top: 30px;
    left: 80px;
    font-size: 40px;
    letter-spacing: 1px;
}
[data-typing='with'] {
    top: 74px;
    left: 265px;
    font-size: 36px;
    transform: rotate(90deg);
    background: #333;
    color: #fff;
    padding: 0px 4px;
}
/* [data-typing="javascript"] {
    top: 74px;
    left: 229px;
    transform: rotate(90deg);
    font-size: 35px;
} */
[data-typing='typescript'] {
    top: 72px;
    right: 110px;
    font-size: 25px;
}
[data-typing='css'] {
    top: 97px;
    right: 110px;
    font-size: 40px;
}
/* [data-typing="nodejs"] {
    top: 134px;
