    font-size: 35px;
} */
[data-typing='animation'] {
    top: 167px;
    right: 110px;
    font-size: 35px;
}
[data-typing='scenejs'] {
    top: 200px;
    right: 110px;
    font-size: 35px;
}

a {
    color: #333;
    text-decoration: none;
}

.scenejs-editor-timeline {
    position: fixed;
    bottom: 0;
    max-height: 300px;
}
